@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,100&display=swap);
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#root {
  height: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;

  height: 100vh;
}

.QuoteBox {
  width: min(500px, 90%);
}

.QuoteBox-text {
  font-size: 1.5em;
  text-align: center;
}

.QuoteBox-author {
  font-weight: 100;
  font-style: italic;
  text-align: right;
}

.QuoteBox-buttons {
  display: flex;
  justify-content: space-between;
}

.QuoteBox-new-quote {
  color: #fff;
}

.fa-quote-left {
  padding-right: 0.3em;
}

.fa-twitter-square {
  font-size: 2.5em;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.bg-pan-top {
  animation: bg-pan-top 8s both;
}

