/**
 * ----------------------------------------
 * animation bg-pan-top
 * ----------------------------------------
 */
@keyframes bg-pan-top {
  0% {
    background-color: var(--bg-color-one);
    background-position: 50% 100%;
  }
  100% {
    background-color: var(--bg-color-two);
    background-position: 50% 0%;
  }
}

